import DisplayCode from '../../../PublishCommonComponents/DisplayCode';
import { UGC_SUITE_WALL_PREFIX } from '../../../../../Services/Api'
const FireStickCode = ({ data }) => {
    return <div className="card text-decoration-none">
        <div className="card-body p-lg-7">
            <div className="m-auto">
                <div className="embed_code">
                    <h5 className="mb-5 fw-bolder">Steps To Display Social Wall With FireTV Stick</h5>
                    <h6>Step 1 : Collect The Generated Social Wall Display Code</h6>
                    <DisplayCode displayCode={data && Object.keys(data).length > 0 ? `${UGC_SUITE_WALL_PREFIX}${data.wallId}` : null} />
                    <h6 className="mb-5">Step 2 : Display Your Social Wall Using Amazon FireTV Stick</h6>
                    <ul className="list-unstyled list-points fs-7">
                        <li className="mb-3">Go to your Amazon FireTV Stick's home and search for 'Tagbox Display'</li>
                        <li className="mb-3">Install Tagbox Display's app and launch it</li>
                        <li className="mb-3">Now, enter the display code in the app</li>
                        <li className="mb-3">Your social wall will appear on the next screen</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
}
export default FireStickCode;